          <section id="about" className="about">
            <div className="container">
              <div className="row">
                <div className="col-lg-5 col-md-6">
                  <div className="about-img">
                    <img src="assets/images/aboutimg.jpg" alt="" />
                  </div>
                </div>

                <div className="col-lg-7 col-md-6">
                  <div className="about-content">
                    <h2>
                      <span className="text-danger h2">
                        <strong>Who are We</strong>
                      </span>
                    </h2>

                    <p
                      className="h4"
                      style={{ "margin-top": "20px" }}
                      data-aos="fade-down"
                    >
                      eKalakaar (eK) aims to create the first and single largest
                      integrated digital platform and ecosystem for Indian
                      traditional performing artists. eK will connect talented,
                      ambitious, aspiring traditional performing artists
                      (opportunity seekers) with patrons (talent seekers) eK
                      will help artists in creating a digital presence, building
                      a personal brand, promoting their visibility and upgrading
                      their skills for accessing new work and collaboration
                      opportunities that help them grow !
                      <br />
                      eKalakaar.com aims for mainstreaming the traditional
                      performing art in the world focusing on India. We want to
                      create a creative and business environment by bringing the
                      artists and technicians as well as filmmakers, production
                      houses as well as customers to a common platform for
                      mutual benefit. We understand the artist and market can
                      come together for creating powerful content, which is
                      useful for a larger audience.
                      <br />
                      <br />
                      eKalakaar.com promotes the traditional performing art by
                      democratising the art, wherein the artists have quality of
                      life and dignity. Considering the grow of the media sector
                      and hunger for audience friendly content in one hand, and
                      the huge available content in traditional performing art
                      sector.
                      <br />
                      <br />
                      {/* Actors, for example, may audition for and perform in
                      plays, films, and television shows. Singers may perform in
                      concerts, recording studios, and on television shows.
                      Dancers may perform in live shows, music videos, and
                      movies. Visual artists may create and exhibit their work
                      in galleries, museums, and other venues. In general,
                      kalakaars spend a lot of time rehearsing, practicing and
                      perfecting their craft, and they may work long hours and
                      travel frequently to perform or exhibit their work. */}
                    </p>
                  </div>
                </div>
                <div className="row mt-4  text-center">
                  <h2 className="text-danger h2 ">
                    <strong>Why we are unique</strong>
                  </h2>
                  <p
                    className="text-start h4"
                    style={{ "text-decoration": "none", "margin-left": "20px" }}
                    data-aos="fade-up"
                  >
                    Kalkaar is a type of traditional Indian art that has been
                    practiced for centuries. The art form
                    is believed to have originated in the  Various state of 
                    India, and is still practiced today. Kalkaar art is created
                    using a variety of technique The Art are often
                    intricate and detailed, and can range from simple to Modern art performed by people to 
                    have their own custom and art Practised.
                    <br />
                    <br />
                     The art form is often
                    used to express religious and cultural beliefs, and is a
                    popular choice for weddings and other special occasions.
                    Kalkaar art is a unique and beautiful form of art that has
                    been passed down through generations. It is a reminder of
                    the rich cultural heritage of India, and a reminder of the
                    importance of preserving traditional art forms.
                  </p>
                </div>
              </div>
            </div>
          </section>
          {/* <!-- End About Section --> */}
